<template>
  <div>
    <mrc_ess_iconGroup :is_disabled='readonly'
                       :showReturn='false'
                       :show_del="false"
                       @handleEdit='handleEdit'></mrc_ess_iconGroup>

    <v-card>
      <v-card-title>

      </v-card-title>
      <v-card-text>
        <h2 style='color:rgb(153, 153, 153)'
            class="mt-4">
          {{$t('label_Password_strength_options')}}
        </h2>
        <div style="border:2px solid #eeeef0;border-radius:10px;"
             class="pl-7 pt-4 mt-5 dvbox">
          <v-checkbox v-model="list.security_upper"
                      color='#757575'
                      :readonly="readonly"
                      :true-value="1"
                      :false-value="0"
                      :label="$t('label_The_capital_letters')"></v-checkbox>
          <v-checkbox v-model="list.security_lower"
                      :label="$t('label_Lowercase_letters')"
                      :readonly="readonly"
                      :true-value="1"
                      :false-value="0"
                      color='#757575'></v-checkbox>
          <v-checkbox v-model="list.security_number"
                      :label="$t('label_digital')"
                      :true-value="1"
                      :false-value="0"
                      :readonly="readonly"
                      color='#757575'></v-checkbox>
          <v-checkbox v-model="list.security_other"
                      :label="$t('label_Other_characters')"
                      :true-value="1"
                      :false-value="0"
                      :readonly="readonly"
                      color='#757575'></v-checkbox>
          <div class="d-flex">
            <v-checkbox v-model="list.security_password"
                        :label="$t('label_Password_length')"
                        :true-value="1"
                        :false-value="0"
                        :readonly="readonly"
                        color='#757575'>
            </v-checkbox>
            <mrc_ess_NumInput height='32'
                              class="mt-1"
                              textAlign='center'
                              :style="{width:readonly?'64px':'128px'}"
                              v-model="list.security_password_length"
                              label=' '
                              :readonly='readonly'></mrc_ess_NumInput>
            <div style="font-size: 16px;height: 32px;line-height: 45px;">位</div>
          </div>
        </div>

        <h2 style='color:rgb(153, 153, 153)'
            class="mt-9">
          {{$t('label_Password_security_options_Settings')}}
        </h2>
        <div style="border:2px solid #eeeef0;border-radius:10px;"
             class="pt-3 mt-5 dvbox">
          <div class="d-flex rowbox">
            <v-checkbox v-model="list.security_update_frequency"
                        :label="$t('label_every')"
                        :true-value="1"
                        :false-value="0"
                        :readonly="readonly"
                        color='#757575'>
            </v-checkbox>
            <mrc_ess_NumInput height='32'
                              class="mt-1"
                              textAlign='center'
                              :style="{width:readonly?'64px':'128px'}"
                              v-model="list.security_update_frequency_day"
                              label=' '
                              :readonly='readonly'></mrc_ess_NumInput>
            <div style="font-size: 16px;height: 32px;line-height: 45px;">天更新密码一次</div>
          </div>
          <div class="rowbox">
            <v-checkbox v-model="list.security_repeate_history_password"
                        :label="$t('label_Allows_the_reuse_of_historical_passwords')"
                        color='#757575'
                        :true-value="1"
                        :false-value="0"
                        :readonly="readonly">
            </v-checkbox>
          </div>
          <div class="rowbox d-flex flex-column">
            <span>{{$t('label_How_many_minutes_does_it_take_to_lock_out_an_account_by_continuously_typing_the_wrong_password')}}</span>
            <span class="mb-3">
              <v-icon>x</v-icon>
              {{$t('label_Permanent_lock_requires_me_to_notify_the_administrator_to_remove_or_the_initial_password')}}
            </span>
            <div class="mb-2">
              <v-data-table :headers="headers"
                            :items="desserts"
                            hide-default-footer
                            class="elevation-1">
                <template v-slot:item.errNum='{item}'>
                  <div class="d-flex">
                    <mrc_ess_NumInput height='32'
                                      textAlign='center'
                                      :style="{width:readonly?'50px':'153px'}"
                                      v-model="number"
                                      label=' '
                                      :readonly='readonly'></mrc_ess_NumInput>
                    <span style="height: 30px;line-height:30px;display:block"
                          class="gray--text">{{$t('label_to')}}</span>
                    <mrc_ess_NumInput height='32'
                                      textAlign='center'
                                      :style="{width:readonly?'50px':'153px'}"
                                      v-model="number"
                                      label=' '
                                      :readonly='readonly'></mrc_ess_NumInput>
                  </div>
                </template>
                <template v-slot:item.lockType>
                  <v-select :items="desserts"
                            :disabled='readonly'
                            label=""></v-select>
                </template>
                <template v-slot:item.lockTime>
                  <div class="d-flex">
                    <mrc_ess_NumInput height='32'
                                      textAlign='center'
                                      :style="{width:readonly?'50px':'153px'}"
                                      v-model="number"
                                      label=' '
                                      :readonly='readonly'></mrc_ess_NumInput>
                    <div style="font-size: 16px;height: 32px;line-height: 32px;">分钟</div>
                  </div>
                </template>
              </v-data-table>
            </div>
          </div>
          <div class="rowbox">
            <v-checkbox v-model="noticeAdmin"
                        :label="$t('label_Notify_the_administrator_when_the_configuration_password_is_reset')"
                        color='#757575'
                        :true-value="1"
                        @change="noticeAdminChange"
                        :false-value="0"
                        :readonly="readonly">
            </v-checkbox>
            <div class="pl-8">
              <v-checkbox v-model="list.security_notice_email"
                          :label="$t('label_Notify_by_mail')"
                          :readonly="readonly || !Boolean(noticeAdmin)"
                          :true-value="1"
                          :false-value="0"
                          color='#757575'>
              </v-checkbox>
              <v-checkbox v-model="list.security_notice_message"
                          :label="$t('label_Notify_with_a_system_message')"
                          color='#757575'
                          :true-value="1"
                          :false-value="0"
                          :readonly="readonly || !Boolean(noticeAdmin)">
              </v-checkbox>
            </div>
          </div>
        </div>
      </v-card-text>
      <v-card-actions>
        <mrc_ess_submitBtn @cancel='cancel'
                           v-if="!readonly"
                           @success='success'></mrc_ess_submitBtn>
      </v-card-actions>
    </v-card>
  </div>
</template>

<script>
import { showPasswordIntensity, updatePasswordIntensity } from '@/service/api/passwordIntensite'
export default {
  components: {},
  props: {},
  data () {
    return {
      searchValue: 'xaxa',
      number: 1,
      readonly: false,
      list: {
        uuid: '',
        security_id: '',
        security_upper: 0,//是否大写字母 
        security_lower: 0,//是否小写字母
        security_number: 0,//是否数字
        security_other: 0,//是否其他字母
        security_password: 0,//是否需要密码长度
        security_password_length: 0,//密码长度位数
        security_update_frequency: 0,//是否密码更新频率 1是 0否
        security_update_frequency_day: 0,//	密码更新频率天数
        security_repeate_history_password: 0,//是否允许重用历史密码
        security_notice_email: 0,//是否以邮件通知管理员 1是 0否
        security_notice_message: 0//是否以消息通知管理员 1是 0否
      },
      noticeAdmin: 1,
      headers: [
        { text: this.$t('label_Wrong_number'), align: 'start', value: 'errNum', },
        { text: this.$t('label_For_the_time_being') + '/永久', value: 'lockType' },
        { text: this.$t('label_Locking_time'), value: 'lockTime', align: 'start', },
      ],
      desserts: [
        {
          errNum: '2',
          lockType: 'x',
          lockTime: 'xax',
          index: 1
        },
        {
          errNum: '2',
          lockType: 'x',
          lockTime: 'xax',
          index: 2
        },
        {
          errNum: '2',
          lockType: 'x',
          lockTime: 'xax',
          index: 3
        }
      ]
    };
  },
  computed: {},
  created () { },
  watch: {},
  async mounted () {
    this.readonly = this.$store.state.iconstatus
    await this.getPasswordIntensity()
    // await this.national('mrbase_sys_dict')
    // console.log(this.national_fields)
  },

  methods: {
    async getPasswordIntensity () {
      const res = await showPasswordIntensity({ org_level_uuid: this.$store.state.uuid })
      if (res.code == 200) {
        this.list = res.data
      }
    },
    noticeAdminChange (val) {
      if (!val) {
        this.list.security_notice_message = 0
        this.list.security_notice_email = 0
      }
    },
    search (v) {
      console.log('搜索点击', v);
    },
    keydown (v) {
      console.log('回车', v);
    },
    ds () {
      console.log(this.searchValue);
    },
    handleEdit () {
      this.readonly = !this.readonly
      this.$store.commit('set_iconstatus', this.readonly)  //行内进图标显示
    },
    cancel () {
      this.$myConfirm({ content: this.$t('message_Confirm_the_cancellation_of_the_edit') })
        .then(async () => {
          this.readonly = !this.readonly
          await this.getPasswordIntensity()
        }).catch(err => { })
    },
    async success () {
      const res = await updatePasswordIntensity({
        org_level_uuid: this.$store.state.uuid,
        // security_id: this.list.security_id,
        security_upper: this.list.security_upper,
        security_lower: this.list.security_lower,
        security_number: this.list.security_number,
        security_other: this.list.security_other,
        security_password: this.list.security_password,
        security_password_length: this.list.security_password_length,
        security_update_frequency: this.list.security_update_frequency,
        security_update_frequency_day: this.list.security_update_frequency_day,
        security_repeate_history_password: this.list.security_repeate_history_password,
        security_notice_email: this.list.security_notice_email,
        security_notice_message: this.list.security_notice_message,
      })
      if (res.code == 200) {
        // this.$Message({ text: , color: 'rgb(0,0,0,0.78)' })
        // this.$Message({ text:  })
        this.readonly = !this.readonly
      }
    }
  },
};
</script> 

<style scoped>
.v-input--selection-controls {
  margin: 0;
  padding: 0;
}
.dvbox /deep/ .v-input__control {
  padding-top: 9px;
}
.rowbox {
  border-bottom: 2px solid #eeeef0;
  width: 100%;
  padding: 0 28px;
  margin: 10px 0px;
}
.v-data-table {
  background-color: #f5f5f5;
}
</style>
